                        b_mut = updateData((inputRecord = b.Data, new GlCanvasData(x, inputRecord.IsDirty, inputRecord.Context, inputRecord.Attributes, inputRecord.Params, inputRecord.PixelStorageParams, inputRecord.CanvasBackground, inputRecord.ClearMask, inputRecord.ClearCanvas, inputRecord.WorldBounds, inputRecord.CanvasBounds, inputRecord.Scenes)));
