export function create(canvasId, props) {
    const canvas = document.getElementById(canvasId);
    const canvasBounds = BoundsModule_boundsSize(canvas.width, canvas.height);
    return build(apply(props, new Builder(canvas, empty(), new GlCanvasData(canvasId, true, null, new WebglContextAttributes(true, false, true, true, false, "default", true, false, false), GlCanvasParams_$ctor(), empty(), Vec4_Create(), 16384, false, Bounds__Clone(canvasBounds), canvasBounds, []))));
}

function clearCanvas(data) {
    const gl = data.Context;
    const cc = Vec4__get_Values(data.CanvasBackground);
    gl.clearColor(cc[0], cc[1], cc[2], cc[3]);
    gl.clear(data.ClearMask);
}

export function render(data) {
    if (data.IsDirty) {
        const msg = toText(interpolate("webgl.%P().render", [data.Name]));
        if (Debug_enabled()) {
            if (Debug_collapsible()) {
                if (Debug_logCollapsed()) {
                    console.groupCollapsed(msg);
                }
                else {
                    console.group(msg);
                }
            }
            else {
                if (Debug_enabled()) {
                    toConsole(interpolate("%P()%P()", [Debug_indent(), msg]));
                }
                if (!Debug_collapsible()) {
                    Debug_indent(Debug_indent() + "  ", true);
                }
            }
        }
        if (data.ClearCanvas) {
            clearCanvas(data);
        }
        data.Scenes.forEach((data_1) => {
            render_1(data_1);
        });
        if (Debug_collapsible()) {
            console.groupEnd();
        }
        else {
            Debug_indent(substring(Debug_indent(), 2), true);
        }
    }
    clean(data);
}

