export function GlCommon_splitName(name) {
    const pair = split(name, ["."], null, 0);
    return [pair[0], pair[1]];
}

export function GlCommon_getAttributeData(attribute) {
    let pattern_matching_result, a;
    if (attribute.tag === 1) {
        pattern_matching_result = 0;
        a = attribute.fields[0];
    }
    else {
        pattern_matching_result = 0;
        a = attribute.fields[0];
    }
    switch (pattern_matching_result) {
        case 0: {
            return a;
        }
    }
}

export function GlCommon_getUbo(name, data) {
    const matchValue = tryFind((u) => (GlUboData__get_Name(u) === name), data.Ubos);
    if (matchValue == null) {
        throw (new Error(toText(interpolate("Ubo \u0027%P()\u0027 not found", [name]))));
    }
    else {
        const ubo = matchValue;
        return ubo;
    }
}

export function GlCommon_tryGetUbo(name, data) {
    return tryFind((u) => (GlUboData__get_Name(u) === name), data.Ubos);
}

